import { Component, OnInit, ElementRef } from '@angular/core';
import { MovieDataService } from '../movie-data.service';
import { Router } from '@angular/router';
declare var Swiper: any;

@Component({
  selector: 'app-news-boxffice',
  templateUrl: './news-boxffice.component.html',
  styleUrls: ['./news-boxffice.component.css']
})
export class NewsBoxfficeComponent implements OnInit {
  public swiper;
  public catId;
  public myMovies = [];
  public myNews = [];
  constructor(private elementRef: ElementRef, private data: MovieDataService, private router: Router) { }

  ngOnInit() {
    this.data.getData().subscribe(
      data => {
        let assistArray = [];
        assistArray = data;
        let x = assistArray.find(item => item.catId == 8);
        
        let z = x.moviesList;
        this.myMovies = z;
        

        let a = assistArray.find(item => item.catId == 10);
        let e = a.newsList;
        let catId = a.catId;
        this.myNews = e;
        this.catId = catId;


        setTimeout(() => {
          this.swiper = new Swiper(this.elementRef.nativeElement.querySelector('.swiper-container'), {
            direction: 'vertical',
            slidesPerGroup: 3,
            slidesPerView: 3,
            spaceBetween: 16,
            pagination: {
              el: '.swiper-pagination',
              clickable: true,
            },
          });
        }, 0);
      }
    )   
  }

  moreDetail(news) {
    let newsId = news.newsId;
    this.router.navigate(["/newsRoom", newsId, {"CategoryId": this.catId}]);
  }

}
