import { Component, OnInit, ElementRef } from '@angular/core';
import { MovieDataService } from '../movie-data.service';
declare var Swiper: any;

@Component({
  selector: 'app-coming-movies',
  templateUrl: './coming-movies.component.html',
  styleUrls: ['./coming-movies.component.css']
})
export class ComingMoviesComponent implements OnInit {
  public swiper;
  public myMovies = [];
  constructor(private elementRef: ElementRef, private data: MovieDataService) { }

  ngOnInit() {
    this.data.getData().subscribe(
      data => {
        let assistArray = [];
        assistArray = data;
        let x = assistArray.find(item => item.catId == 9);
        let z = x.moviesList;
        this.myMovies = z;


        setTimeout(() =>{ 
          this.swiper = new Swiper(this.elementRef.nativeElement.querySelector('.swiper-container'), {
            slidesPerView: 5,
            spaceBetween: 0,
            centeredSlides: true,
            
          });
        }, 0);
      }
    )

   
  }

}
