import { Component, OnInit, ElementRef } from '@angular/core';
import { MovieDataService } from '../movie-data.service';
import { Router } from '@angular/router';
declare var Swiper: any;

@Component({
  selector: 'app-popular',
  templateUrl: './popular.component.html',
  styleUrls: ['./popular.component.css']
})
export class PopularComponent implements OnInit {
  public swiper;
  public listMovies = [];
  public twoLine = {
    textOverflow: "ellipsis",
    overflow: "hidden",
    display: "-webkit-box",
    OellipsisLastline: 2,
    webkitLineClamp: 2,
    mozBineBlamp: 2,
    webkitBoxOrient: "vertical",
    mozBoxOrient: "vertical",
    msBoxOrient: "vertical",
    boxOrient: "vertical",
  }
  public catId;
  constructor(private elementRef: ElementRef, private data: MovieDataService, private router: Router) { }

  ngOnInit() {
    this.data.getData().subscribe(
      data => {
        let assistArray = [];
        assistArray = data;
        let arrayById = assistArray.find(item => item.catId == 7);
        let arrayId = arrayById.catId;
        let x = arrayById.moviesList;
        this.listMovies = x;
        this.catId = arrayId;

        setTimeout(() => {
          this.swiper = new Swiper(this.elementRef.nativeElement.querySelector('.swiper-container'), {
            slidesPerView: 3,
            spaceBetween: 8,
            centeredSlides: true,
            
          });
        }, 0);
      }
    )  
  }

  goDetail(movie) {
    let movieId = movie.movieId; 
    this.router.navigate(["/details", movieId, {"CategoryId": this.catId}]);
  }

}
