import { Component, OnInit, ElementRef } from '@angular/core';
import { MovieDataService } from '../movie-data.service';
import { ActivatedRoute, ParamMap } from '@angular/router';
declare var Swiper: any;

@Component({
  selector: 'app-movie-details',
  templateUrl: './movie-details.component.html',
  styleUrls: ['./movie-details.component.css']
})
export class MovieDetailsComponent implements OnInit {
  public movieIdd;
  public catId;
  public swiper;
  public movie = [];
  public others = [];
  constructor(private elementRef: ElementRef, private data: MovieDataService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.paramMap.subscribe((param: ParamMap) => {
      let movieId = param.get("id");
      let catIdd = param.get("CategoryId");
      this.movieIdd = movieId;
      this.catId = catIdd;
    });

    this.data.getData().subscribe(
      data => {
        let assistArray = [];
        assistArray = data;

        let x = assistArray.find(item => item.catId == this.catId);
        let z = x.moviesList;
        let finalDetail = z.find(item => item.movieId == this.movieIdd);
        this.movie = finalDetail;
        
        let neewArray = [];
        neewArray = data;
        let otherMovie = neewArray.find(item => item.catId == 7);
        let u = otherMovie.moviesList;
        this.others = u;

        setTimeout(() => {
          this.swiper = new Swiper(this.elementRef.nativeElement.querySelector('.swiper-container'), {
            slidesPerView: 5,
            spaceBetween: 18,
            autoplay: {
              delay: 2000,
              disableOnInteraction: false,
            },
          });
        }, 0);
      } 
    )
    
    }



  
}
