import { Component, OnInit, ElementRef } from '@angular/core';
import { MovieDataService } from '../movie-data.service';
import { Router } from '@angular/router';
declare var Swiper: any;

@Component({
  selector: 'app-homeslider',
  templateUrl: './homeslider.component.html',
  styleUrls: ['./homeslider.component.css']
})
export class HomesliderComponent implements OnInit {
  public swiper;
  public slideMovies = [];
  public twoLine = {
    textOverflow: "ellipsis",
    overflow: "hidden",
    display: "-webkit-box",
    OellipsisLastline: 2,
    webkitLineClamp: 2,
    mozBineBlamp: 2,
    webkitBoxOrient: "vertical",
    mozBoxOrient: "vertical",
    msBoxOrient: "vertical",
    boxOrient: "vertical",
  }
  public catId;
  constructor(private elementRef: ElementRef, private myData: MovieDataService, private router: Router) { }

  ngOnInit() {
    this.myData.getData().subscribe(
      data => {
        let assistArray = [];
        assistArray = data;
        let arrayByCatName = assistArray.find(item => item.catName == 'SlideMovies');
        let arrayId = arrayByCatName.catId;
        let arraylist = arrayByCatName.moviesList;
        this.slideMovies = arraylist;
        this.catId = arrayId;
        
        
        
        setTimeout(() =>{ 
          this.swiper = new Swiper(this.elementRef.nativeElement.querySelector('.swiper-container'), {
            pagination: {
              el: '.swiper-pagination',
              dynamicBullets: true,
            },
          });
        }, 0);
      }
      
      
    )
  }

  goDetail(movie) {
    let movieId = movie.movieId; 
    this.router.navigate(["/details", movieId, {"CategoryId": this.catId}]);
  }
  
}
